import React, { useState } from 'react'
import { Form,Button,Table } from 'react-bootstrap'

export default function User() {

  const[result,setResult] = useState(" ")
  const handleClick = (e)=>{
    setResult(result + e.target.name)
  }
  const clearAll = () => {
    setResult(" ");
  }
  const clear = () => {
    setResult(result.slice(0, -1));
  }
  const cal = () => {
    try{
      setResult(eval(result).toString());
    }
    catch(error){
      setResult("ERROR")
    }
  }

  return (
    <div className='container my-5' style={{width:'20rem'}}>
      <Form>
        <Form.Group controlId="exampleForm.ControlInput1">
          <Form.Control type="text" size="sm" value={result} onChange={handleClick}/>
        </Form.Group>
      </Form>
      <Table striped hover variant="dark" className='my-1'>
        <tbody>
          <tr>
            <td>
              <Button onClick={clearAll} variant="secondary" size="sm"  className='mx-2' style={{width:'7.5rem'}}>Clear</Button>
              <Button onClick={clear} variant="secondary" size="sm" className='mx-1' style={{width:'4rem'}}>C</Button>
              <Button name="+" onClick={handleClick} variant="light" size="sm" className='mx-1' style={{width:'4rem'}}>+</Button>
            </td>
          </tr>
          <tr>
            <td>
              <Button name="1" onClick={handleClick} variant="light" size="sm"  className='mx-1' style={{width:'4rem'}}>1</Button>
              <Button name="2" onClick={handleClick} variant="light" size="sm" className='mx' style={{width:'4rem'}}>2</Button>
              <Button name="3" onClick={handleClick} variant="light" size="sm" className='mx-1' style={{width:'4rem'}}>3</Button>
              <Button name="-" onClick={handleClick} variant="light" size="sm" className='mx' style={{width:'4rem'}}>-</Button>
            </td>
          </tr>
          <tr>
            <td>
              <Button name="4" onClick={handleClick} variant="light" size="sm"  className='mx-1' style={{width:'4rem'}}>4</Button>
              <Button name="5" onClick={handleClick} variant="light" size="sm" className='mx' style={{width:'4rem'}}>5</Button>
              <Button name="6" onClick={handleClick} variant="light" size="sm" className='mx-1' style={{width:'4rem'}}>6</Button>
              <Button name="*" onClick={handleClick} variant="light" size="sm" className='mx' style={{width:'4rem'}}>*</Button>
            </td>
          </tr>
          <tr>
            <td>
              <Button name="7" onClick={handleClick} variant="light" size="sm"  className='mx-1' style={{width:'4rem'}}>7</Button>
              <Button name="8" onClick={handleClick} variant="light" size="sm" className='mx' style={{width:'4rem'}}>8</Button>
              <Button name="9" onClick={handleClick} variant="light" size="sm" className='mx-1' style={{width:'4rem'}}>9</Button>
              <Button name="/" onClick={handleClick} variant="light" size="sm" className='mx' style={{width:'4rem'}}>/</Button>
            </td>
          </tr>
          <tr>
            <td>
              <Button name="0" onClick={handleClick} variant="light" size="sm" className='mx-2' style={{width:'4rem'}}>0</Button>
              <Button name="." onClick={handleClick} variant="light" size="sm" className='mx-1' style={{width:'4rem'}}>.</Button>
              <Button onClick={cal} variant="secondary" size="sm" className='mx-1' style={{width:'7.5rem'}}>=</Button>
            </td>
          </tr>
        </tbody>
      </Table>
    </div>
  )
}
